"use client";
import { useEffect, useState } from "react";
import { Dispatch, SetStateAction, useMemo } from "react";
import { hideLoader } from "../BlankPageLoader";
import Console from "@/utils/console";
import useGlobalContext, {
  countSummaryType,
  myAccessType,
  navSearchBarType,
} from "../GlobalContext";
import { mutate, useSWR } from "@/utils/api-handler/swr";
import { createId, isNull, notNull } from "@/utils/more";
import axios from "@/utils/api-handler/axios";
import IconSkeleton from "@/components/icons/IconSkeleton";
import GoogleIcons from "@/components/icons/GoogleIcons";
import SpinLoader from "@/components/preloader/SpinLoader";
import GithubIcons from "@/components/icons/GithubIcons";
import SvgCat from "@/components/svg-image/Cat";
import api from "@/app/api/endpoints";

let cacheSession: any;

export default function Page() {
  const {
    myAccess,
    setActiveTab,
    doRefetch,
    countSummary,
    navSearchBar,
    setNavSearchBar,
  } = useGlobalContext();
  return useMemo(
    () => (
      <_Page
        myAccess={myAccess}
        setActiveTab={setActiveTab}
        doRefetch={doRefetch}
        countSummary={countSummary}
        navSearchBar={navSearchBar}
        setNavSearchBar={setNavSearchBar}
      />
    ),
    [
      myAccess,
      setActiveTab,
      doRefetch,
      countSummary,
      navSearchBar,
      setNavSearchBar,
    ]
  );
}

function _Page({
  myAccess,
  setActiveTab,
  doRefetch,
  countSummary,
  navSearchBar,
  setNavSearchBar,
}: {
  myAccess: myAccessType;
  setActiveTab: Dispatch<SetStateAction<string>>;
  doRefetch: boolean;
  countSummary: countSummaryType;
  navSearchBar: navSearchBarType;
  setNavSearchBar: Dispatch<SetStateAction<navSearchBarType>>;
}) {
  Console.component("Page");

  useEffect(() => {
    document.title = "Session";
    setActiveTab("session");
  }, [setActiveTab]);

  const [data, setData] = useState<
    | undefined
    | {
        id: number;
        session: string;
        isMuted: boolean;
        usedByUser: number;
        usedByClass: number;
        isEdited: boolean;
        isNew?: boolean;
      }[]
  >(undefined);
  const [filter, setFilter] = useState({
    all: true,
    mutedOnly: false,
    isLoading: false,
  });

  const [res, mutateSession] = useSWR(
    "/api/session/read-all?searchKey=" +
      navSearchBar.keyword.trim() +
      "&&isMuted=" +
      (filter.mutedOnly === true ? 1 : 0)
  );
  useEffect(() => {
    if (!res) return;
    if (res.status === 200) {
      setData(res.data);
      cacheSession = res.data;
      setNavSearchBar((prev) => ({ ...prev, isLoading: false }));
      setFilter((prev) => ({ ...prev, isLoading: false }));
    }
    hideLoader();
  }, [res]);

  // Reload data fetchers
  useEffect(() => {
    if (doRefetch) mutateSession();
  }, [doRefetch, mutateSession]);

  // Switch filter option
  function switchFilterOpt(keyName: "all" | "mutedOnly") {
    if (!filter[keyName]) {
      setFilter((prev) => {
        const o: any = { ...prev };
        for (const k in o) {
          o[k] = false;
        }
        o[keyName] = true;
        o.isLoading = true;
        return o;
      });
    }
  }

  // Add new session
  function addNewTableData() {
    setShowSaveButton(true);
    setData((prev) => [
      {
        id: createId(),
        session: "",
        isMuted: false,
        usedByUser: 0,
        usedByClass: 0,
        isEdited: true,
        isNew: true,
      },
      ...(prev || []),
    ]);
  }

  // Edit session related--->
  const [showSaveButton, setShowSaveButton] = useState(false);

  function switchEditMode(index: number) {
    setShowSaveButton(true);
    setData((prev) =>
      prev?.map((record, i) => {
        if (i !== index) return record;
        else return { ...record, isEdited: !record.isEdited };
      })
    );
  }

  function writingOnInputField(index: number, newRecord: any) {
    setData((prev) =>
      prev?.map((record, i) => {
        if (i !== index) return record;
        else return newRecord;
      })
    );
  }
  // <---Edit session related

  // Save the updated table data
  const [isSaving, setIsSaving] = useState(false);
  async function saveUpdatedFormData() {
    setIsSaving(true);
    const res = await axios.post(api.saveUpdatedSessionTableData, data);
    if (res.status === 200) await mutateSession();
    setIsSaving(false);
    setShowSaveButton(false);
  }

  /*****
   * Deleting session
   ******/
  const [deletingId, setDeletingId] = useState<number[]>([]);
  async function doDelete(id: number) {
    setDeletingId((prev) => [...prev, id]);
    await axios.delete(api.deleteSession + id);
    await Promise.all([mutateSession(), mutate(api.readCountSummary)]);
    setDeletingId((prev) => prev.filter((id) => id != id));
  }

  // Update Mute-Unmute status
  const [muteUnmuteId, setMuteUnmuteId] = useState<number[]>([]);
  async function switchMuteUnmute(id: number) {
    setMuteUnmuteId((prev) => [...prev, id]);
    await axios.get(api.switchMuteUnmute + id);
    await mutateSession();
    setMuteUnmuteId((prev) => prev.filter((id) => id != id));
  }

  return (
    // Here margin bottom 64px so that save button do not hide the content of last row
    <div className="w-full h-full mb-16 px-8 py-6 bg-white dark:bgBlack2 rounded-lg">
      <div className="w-[700px]">
        {/* Title */}
        <div className="flex items-center gap-2 mb-2 text-gray-600">
          <div className="fontGs text-2xl">Session</div>
          <div className="text-lg">({countSummary.session.total})</div>
        </div>

        {/* Table header */}
        <div className="px-2 py-2.5 pr-6 flex items-center gap-2 border-b border-gray-300">
          <div className="w-[25%] pl-4 fontGsm">Session</div>
          <div className="w-[25%] fontGsm opacity-0">Used by users</div>
          <div className="w-[25%] fontGsm opacity-0">Used by classes</div>
          <div className="w-[25%]">
            {myAccess?.createSession && (
              <div className="flex justify-end items-center gap-4">
                <IconSkeleton title="Add session" onClick={addNewTableData}>
                  <GoogleIcons.plus className="fill-gray-500" />
                </IconSkeleton>
              </div>
            )}
          </div>
        </div>

        {data !== undefined && (
          <>
            <div className="pl-6 p-4 pb-3 flex justify-between items-center">
              {/* Summary */}
              <div className="flex items-center gap-5">
                <div className="fontGsm text-gray-500">
                  Total {data.length}/{countSummary.session.total}
                </div>
                <div className="fontGsm text-gray-500">
                  Muted {countSummary.session.muted}
                </div>
              </div>

              {/* Search keys */}
              <div className="flex justify-center items-center gap-4">
                <div
                  className="cursor-pointer hover:underline flex items-center gap-1"
                  onClick={() => switchFilterOpt("all")}
                >
                  <div
                    className={`fontGsm ${
                      filter.all ? "textBlue" : "text-gray-500"
                    }`}
                  >
                    All session
                  </div>
                  {filter.all &&
                    (filter.isLoading ? (
                      <SpinLoader size={24} />
                    ) : (
                      <GithubIcons.tik className="fillBlue" />
                    ))}
                </div>
                {/*  */}
                <div
                  className="cursor-pointer hover:underline flex items-center gap-1"
                  onClick={() => switchFilterOpt("mutedOnly")}
                >
                  <div
                    className={`fontGsm ${
                      filter.mutedOnly ? "textBlue" : "text-gray-500"
                    }`}
                  >
                    Muted only
                  </div>
                  {filter.mutedOnly &&
                    (filter.isLoading ? (
                      <SpinLoader size={24} />
                    ) : (
                      <GithubIcons.tik className="fillBlue" />
                    ))}
                </div>
                {/*  */}
              </div>
            </div>

            {isNull(data) ? (
              <div className="w-full flex justify-center items-center py-16">
                <div className="w-fit h-fit flex flex-col justify-center items-center gap-6">
                  <SvgCat className="w-[170px]" />
                  <div className="fontGsm text-gray-500">No record found</div>
                </div>
              </div>
            ) : (
              <div className="h-fit">
                {data.map((record, index) => {
                  return (
                    <div
                      key={index}
                      className={`group/menu px-2 py-2.5 flex items-center gap-2 ${
                        !record.isEdited &&
                        "hover:bgLightBlue dark:hover:bgBlack3"
                      } rounded-md`}
                    >
                      {
                        // Show the actual session data
                        !record.isEdited ? (
                          <>
                            <div className="w-[25%] pl-4 break-all">
                              {record.session}
                            </div>
                            <div className="w-[25%] break-all opacity-0">
                              {record.usedByUser}
                            </div>
                            <div className="w-[25%] opacity-0">
                              {record.usedByClass}
                            </div>
                            <div className="w-[25%] pr-4 flex justify-end items-center gap-4">
                              {(() => {
                                if (
                                  deletingId.includes(record.id) ||
                                  muteUnmuteId.includes(record.id)
                                )
                                  return <SpinLoader size={28} />;
                                else
                                  return (
                                    <>
                                      {record.isMuted && (
                                        <div className="fontGsm text-red-600 group-hover/menu:hidden">
                                          Muted
                                        </div>
                                      )}
                                      {myAccess?.editSession && (
                                        <>
                                          <IconSkeleton
                                            title={
                                              record.isMuted ? "Unmute" : "Mute"
                                            }
                                            className="hidden group-hover/menu:block"
                                            onClick={() =>
                                              switchMuteUnmute(record.id)
                                            }
                                          >
                                            <GoogleIcons.block className="fill-gray-500" />
                                          </IconSkeleton>
                                          <IconSkeleton
                                            title="Edit"
                                            className="hidden group-hover/menu:block"
                                            onClick={() =>
                                              switchEditMode(index)
                                            }
                                          >
                                            <GoogleIcons.edit className="fill-gray-500" />
                                          </IconSkeleton>
                                        </>
                                      )}
                                      {myAccess?.deleteSession && (
                                        <IconSkeleton
                                          title="Delete"
                                          className="hidden group-hover/menu:block"
                                          onClick={() => doDelete(record.id)}
                                        >
                                          <GoogleIcons.delete className="fill-gray-500" />
                                        </IconSkeleton>
                                      )}
                                    </>
                                  );
                              })()}
                            </div>
                          </>
                        ) : (
                          // Show the edited session data
                          <>
                            <div className="w-[25%] pl-4 break-all">
                              <div className="w-full relative flex flex-col gap-1">
                                <input
                                  type="text"
                                  placeholder="Session"
                                  className="w-full px-0.5 bgLightBlue dark:bgBlack3 border borderLightBlue focus:borderBlue"
                                  value={record.session}
                                  onChange={(e) =>
                                    writingOnInputField(index, {
                                      ...record,
                                      session: e.target.value,
                                    })
                                  }
                                />
                              </div>
                            </div>
                            <div className="w-[25%] break-all">
                              {record.usedByUser}
                            </div>
                            <div className="w-[25%]">{record.usedByClass}</div>
                            <div className="w-[25%] pr-4 flex justify-end items-center gap-4"></div>
                          </>
                        )
                      }
                    </div>
                  );
                })}
              </div>
            )}
          </>
        )}
      </div>

      {(myAccess?.createSession || myAccess?.editSession) && (
        <>
          {/* Save new or edited data */}
          {showSaveButton && (
            <div className="w-full h-16 fixed bottom-0 left-0 bg-white dark:bgBlack3 boxShadow flex justify-center items-center gap-2">
              <button
                className={isSaving ? "btnDisable" : "btn"}
                onClick={() => {
                  if (isSaving) return;
                  saveUpdatedFormData();
                }}
              >
                Save
              </button>
              <button
                className="btn2"
                onClick={() => {
                  setShowSaveButton(false);
                  setData(cacheSession);
                }}
              >
                Cancel
              </button>
            </div>
          )}
        </>
      )}
    </div>
  );
}
